html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 13px;
  color: var(--mine-shaft);
  background-color: var(--color-concrete);
}

img {
  max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1170px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.title {
  position: relative;
  font-weight: 700;
  font-size: 48px;
  line-height: 71px;
  letter-spacing: 3.072px;
  color: var(--mine-shaft);
}

.title::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -5px;
  width: 45px;
  height: 2px;
  background: var(--mine-shaft);
}

@media (max-width: 576px) {
  .title {
    font-size: 40px;
  }
}

.header {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  width: 100%;
}

.header-fixed {
  position: fixed;
  z-index: 500;
  background: linear-gradient(90deg, #4caf50 19%, #777777 50%, #504e4e 81%);
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 10px;
  padding-bottom: 10px;
}

.logo {
  display: inline-block;
  width: 97px;
  height: 30px;
}

.logo svg {
  display: block;
  width: 100%;
  height: 100%;
  fill: var(--color-white);
  object-fit: cover;
}

.burger {
  position: relative;
  display: none;
  width: 25px;
  height: 15px;
}

.burger::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 2px;
  background-color: #4caf50;
}

.burger::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  background-color: #4caf50;
}

.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: 2px;
  background-color: #4caf50;
  transform: translateY(-50%);
}

@media (max-width: 768px) {
  .burger {
    display: block;
  }
}

.nav__close {
  display: none;
  font-size: 14px;
}

@media (max-width: 768px) {
  .nav__close {
    display: block;
  }
}

.nav__list {
  display: flex;
}

@media (max-width: 576px) {
  .nav__list {
    display: block;
  }
}

.nav__item:not(:last-child) {
  margin-right: 45px;
}

@media (max-width: 992px) {
  .nav__item:not(:last-child) {
    margin-right: 35px;
  }
}

@media (max-width: 576px) {
  .nav__item:not(:last-child) {
    margin: 0;
    margin-bottom: 10px;
  }
}

.nav__link--current::before {
  width: 100% !important;
}

@media (max-width: 768px) {
  .nav {
    position: fixed;
    right: 0;
    top: -50px;
    z-index: 999;
    display: inline-flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px;
    width: 100%;
    height: 50px;
    background: linear-gradient(90deg, #504e4e 19%, #777 50%, #4caf50 81%);
    transition: top 0.3s;
  }
}

@media (max-width: 576px) {
  .nav {
    top: -160px;
    height: 160px;
  }
}

.nav--visible {
  top: 0;
  transition: top 0.3s;
}

.main-link {
  position: relative;
  padding-left: 5px;
  font-size: 14px;
  text-transform: uppercase;
  color: var(--color-white);
}

.main-link::before {
  content: "";
  position: absolute;
  right: 0;
  bottom: -3px;
  width: 50%;
  height: 2px;
  background-color: #4caf50;
  transition: width 0.3s;
}

.main-link:hover::before {
  width: 100%;
  transition: width 0.3s;
}

.idea {
  position: relative;
  height: 100vh;
  min-height: 700px;
  background: url("../img/bg-head.png") center no-repeat;
  background-size: cover;
}

.idea::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: linear-gradient(#000 -15%, rgba(0, 0, 0, 0.0006) 100%);
}

.idea__container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  text-align: center;
  user-select: none;
}

.idea__title {
  font-weight: 500;
  font-size: 55px;
  line-height: 107px;
  letter-spacing: 4.608px;
  color: var(--color-white);
}

@media (max-width: 992px) {
  .idea__title {
    font-size: 44px;
  }
}

@media (max-width: 768px) {
  .idea__title {
    font-size: 30px;
    line-height: 55px;
  }
}

@media (max-width: 576px) {
  .idea__title {
    margin-bottom: 10px;
    font-size: 26px;
    line-height: 40px;
  }
}

.idea__descr {
  font-weight: 200;
  font-size: 12px;
  line-height: 27px;
  letter-spacing: 3px;
  color: var(--color-white);
  opacity: 0.8;
}

@media (max-width: 992px) {
  .idea {
    min-height: 500px;
  }
}

@media (max-width: 768px) {
  .idea {
    height: 100vh;
    min-height: auto;
  }
}

.banner-slider {
  overflow: hidden;
  width: 100%;
}

.banner-slider .swiper-slide {
  height: 100%;
}

.idea-pag {
  position: absolute;
  left: 50%;
  bottom: 5%;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translateX(-50%);
}

.idea-pag .swiper-pagination-bullet {
  display: inline-block;
  width: 100%;
  height: 100%;
  font-weight: 700;
  font-size: 18px;
  line-height: 27px;
  letter-spacing: 1.152px;
  color: rgba(255, 255, 255, 0.9);
  background: none;
  transition: color 0.3s;
}

.idea-pag .swiper-pagination-bullet:not(:last-child) {
  margin-right: 25px;
}

.idea-pag .swiper-pagination-bullet-active {
  position: relative;
  padding: 10px;
  font-weight: 700;
  font-size: 36px;
  letter-spacing: 2.304px;
  color: var(--color-white);
  background: none;
}

.idea-pag .swiper-pagination-bullet-active::before {
  content: "";
  position: absolute;
  left: 0;
  top: 26px;
  width: 100%;
  height: 3px;
  background: #4caf50;
}

@media (max-width: 768px) {
  .idea-pag .swiper-pagination-bullet-active {
    font-size: 30px;
  }
}

.sqwd {
  margin-top: 100px;
  margin-bottom: 200px;
}

.sqwd__container {
  position: relative;
  display: flex;
  padding: 100px 15px;
}

@media (max-width: 768px) {
  .sqwd__container {
    padding: 0 15px;
  }
}

.sqwd__content {
  display: flex;
}

.sqwd__info {
  width: 310px;
}

.sqwd__info--two {
  position: absolute;
  right: 0;
  bottom: -30%;
}

@media (max-width: 992px) {
  .sqwd__info--two {
    bottom: 0;
  }
}

@media (max-width: 768px) {
  .sqwd__info--two {
    bottom: 26%;
  }
}

@media (max-width: 576px) {
  .sqwd__info--two {
    display: none;
  }
}

@media (max-width: 768px) {
  .sqwd__info {
    width: 200px;
  }
}

.sqwd__title {
  font-weight: 700;
  font-size: 36px;
  line-height: 53px;
  letter-spacing: 2.304px;
  text-transform: uppercase;
  color: #333;
}

@media (max-width: 768px) {
  .sqwd__title {
    font-size: 30px;
    line-height: 30px;
  }
}

.sqwd__subtitle {
  margin-bottom: 20px;
  font-weight: 300;
  font-size: 42px;
  line-height: 57px;
  letter-spacing: 2.688px;
  text-transform: uppercase;
  color: var(--color-emperor);
}

@media (max-width: 768px) {
  .sqwd__subtitle {
    font-size: 35px;
    line-height: 46px;
  }
}

.sqwd__descr {
  position: relative;
  padding-left: 40px;
  font-weight: 300;
  font-size: 15px;
  line-height: 25px;
  letter-spacing: 1.26px;
  color: var(--color-scorpion);
}

.sqwd__descr::before {
  content: "";
  position: absolute;
  left: 5px;
  top: 15%;
  width: 20px;
  height: 2px;
  background: #353535;
}

@media (max-width: 768px) {
  .sqwd__descr {
    font-size: 13px;
  }
}

.sqwd__img {
  position: absolute;
  left: 17%;
  top: 0;
  z-index: -1;
}

.sqwd__img-one {
  position: relative;
}

.sqwd__img-one::before {
  content: "";
  position: absolute;
  left: 75%;
  top: 25%;
  z-index: -1;
  width: 100%;
  height: 100%;
  background: #4caf50;
  opacity: 0.8;
}

@media (max-width: 992px) {
  .sqwd__img-one::before {
    left: 45%;
    top: 10%;
  }
}

@media (max-width: 768px) {
  .sqwd__img-one {
    width: 200px;
  }
}

@media (max-width: 992px) {
  .sqwd__img {
    left: 25%;
    width: 35%;
  }
}

@media (max-width: 576px) {
  .sqwd__img {
    top: 50%;
  }
}

.sqwd__images {
  position: absolute;
  right: 18%;
  bottom: -50%;
  z-index: -2;
  opacity: 0.8;
}

@media (max-width: 1200px) {
  .sqwd__images {
    display: none;
  }
}

@media (max-width: 992px) {
  .sqwd {
    margin-bottom: 50px;
  }
}

@media (max-width: 576px) {
  .sqwd {
    margin-bottom: 150px;
  }
}

.services {
  padding: 98px 0;
}

.services__title {
  margin-bottom: 80px;
}

@media (max-width: 1200px) {
  .services__title {
    margin-bottom: 45px;
  }
}

.services__item {
  width: 33.33333%;
}

.services__item:not(:last-child) {
  margin-right: 15px;
}

@media (max-width: 768px) {
  .services__item {
    width: 100%;
  }
  .services__item:not(:last-child) {
    margin-bottom: 15px;
  }
}

.services__list {
  display: flex;
  justify-content: space-between;
}

@media (max-width: 768px) {
  .services__list {
    display: block;
  }
}

.services__article {
  position: relative;
  display: inline-block;
  padding: 50px;
  background: var(--color-white);
  transition: box-shadow 0.2s;
  cursor: pointer;
}

.services__article:hover {
  box-shadow: 0 3px 0 0 #70bf73;
  transition: box-shadow 0.2s;
}

.services__article:hover .services-article__link {
  opacity: 1;
  transition: opacity 0.2s;
}

.services__article:hover .services-article__head-icon::before {
  box-shadow: 0 0 20px #dbdbdb;
  transition: box-shadow 0.2s;
}

.services__article:hover svg {
  fill: var(--mine-shaft);
  transition: fill 0.2s;
}

@media (max-width: 992px) {
  .services__article {
    padding: 25px;
  }
}

@media (max-width: 992px) {
  .services {
    padding-bottom: 50px;
  }
}

.services-article__head {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 30px;
}

.services-article__head-title {
  position: relative;
  font-weight: 300;
  font-size: 20px;
  line-height: 30px;
  text-transform: uppercase;
  color: var(--mine-shaft);
}

.services-article__head-title::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -10px;
  width: 35px;
  height: 2px;
  background: var(--mine-shaft);
}

.services-article__head-icon {
  position: relative;
}

.services-article__head-icon::before {
  content: "";
  position: absolute;
  left: -10px;
  top: -9px;
  border-radius: 100%;
  width: 45px;
  height: 45px;
}

.services-article__head svg {
  width: 25px;
  height: 25px;
  fill: #777;
  object-fit: cover;
  transition: fill 0.2s;
}

.services-article__descr {
  margin-bottom: 20px;
  font-family: "Open Sans";
  font-weight: 400;
  font-size: 12px;
  line-height: 25px;
  letter-spacing: 1.008px;
  color: #777;
}

.services-article__link {
